
















import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

@Component({
    name: "MyDatetimePicker"
})
export default class MyDatetimePicker extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    @Prop({ default: () => new Object() })
    option!: { [key: string]: any[] };
}
